<template>
  <div :class="className" :style="{height:height,width:width}"></div>
</template>

<script>
import echarts from 'echarts'
require('echarts/theme/macarons') // echarts theme
import { debounce } from './utils'

export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '200px'
    },
    autoResize: {
      type: Boolean,
      default: true
    },
    chartData: {
      type: Object
    }
  },
  data() {
    return {
      chart: null
    }
  },
  created() {
  },
  mounted() {
    this.initChart()
    if (this.autoResize) {
      this.__resizeHanlder = debounce(() => {
        if (this.chart) {
          this.chart.resize()
        }
      }, 100)
      window.addEventListener('resize', this.__resizeHanlder)
    }

    // 监听侧边栏的变化
    // const sidebarElm = document.getElementsByClassName('sidebar-container')[0]
    // sidebarElm.addEventListener('transitionend', this.__resizeHanlder)
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    if (this.autoResize) {
      window.removeEventListener('resize', this.__resizeHanlder)
    }

    // const sidebarElm = document.getElementsByClassName('sidebar-container')[0]
    // sidebarElm.removeEventListener('transitionend', this.__resizeHanlder)

    this.chart.dispose()
    this.chart = null
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val)
      }
    },
    message: {
      deep: true,
      handler(val) {
        this.setOptions()
      }
    }
  },
  methods: {
    setOptions() {
      var self = this
      this.chart.setOption({
        title: {
            text: self.chartData.title,
            textStyle: {
              fontSize : 14
            }

        },
        tooltip: {
            trigger: 'axis'
        },
        color: ['#61a0a8'],
        xAxis: {
            type: 'category',
            boundaryGap: false,
            data: self.chartData.xData,
            // show: false
        },
        yAxis: {
          type: 'value',
          show: false
        },
        // legend: {
        //       data:self.chartData.legendData,
        // },
        series: self.chartData.seriesData
      }, false)
    },
    initChart() {
      this.chart = echarts.init(this.$el, 'red')
      this.setOptions()
    }
  }
}
</script>



